import React, { useContext } from "react";
import styled from "styled-components";
import { IsDarkContext } from "../context/IsDarkContext";

const Dark = styled.div`
    width: 5.5vw;
    height: 2.5vw;
    position: fixed;
    bottom: 5vh;
    right: 2vw;
    border: none;
    cursor: pointer;
    background-color: ${props=>props.$isDark?'#AAAAAA':'#FFCA42'};
    border-top-left-radius: 5000px;
    border-bottom-left-radius: 5000px;
    border-top-right-radius: 5000px;
    border-bottom-right-radius: 5000px;
`;

const Toggle = styled.div`
    width: 2vw;
    height: 2vw;
    position: absolute;
    top: 0.25vw;
    left: ${props=>props.$isDark?'3.25vw':'0.25vw'};
    border-radius: 50%;
    background-color: ${props=>props.$isDark?'black':'white'};
    display: flex;
    align-items: center;
    justify-content: center;

    & img{
        height: 75%;
    }

    & div{
        position: absolute;
        left: ${props=>props.$isDark?'-120%':'120%'};
        font-weight: 700;
        font-size: 0.9em;
        color: ${props=>props.$isDark?'black':'white'};
    }
`;

const Link = styled.button`
    width: 100%;
    height: 10%;
    flex-shrink: 0;
    margin-bottom: 10%;
    font-size: 1.2em;
    background-color: ${({theme}) => theme.bg5};
    border-style: solid;
    border-color: ${({theme}) => theme.bd1};
    border-radius: 0 4% 4% 0 / 0 10% 10% 0;
    color: white !important;
    cursor: pointer;
    
    &.selected{
        border-left: none;
        background-color: ${props => props.$about
        ? (
            props.aboutindex === 0 ? 'rgb(249,249,249)' :
            props.aboutindex === 1 ? 'rgb(211,235,235)' :
            'rgb(254,247,239)'
        )
        : ({theme}) => theme.bg1
        };
        color: ${props => props.$about
        ? 'black'
        : ({theme}) => theme.color
        } !important;
    }

    &.fullMode{
        border: none;
        background-color: transparent;
        color: ${({theme}) => theme.color} !important;
    }
`;

export const DarkChangeButton = (props) =>{
    const isDark = useContext(IsDarkContext);

    return (
        <Dark onClick={props.onClick} $isDark={isDark}>
            <Toggle $isDark={isDark}>
                <img src={isDark ? './images/dark.png' : './images/light.png'} alt="Dark|Light" />
                <div>{isDark?'Dark':'Light'}</div>
            </Toggle>
        </Dark>
    );
}

export const LinkButton = (props) => {
    return (
        <Link
            onClick={props.onClick}
            className={props.className}
            $about = {props.$about}
            aboutindex = {props.aboutindex}
        >
            {props.children}
        </Link>
    );
}