import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { LinkButton } from "./Button";
import AboutContain from "./contain/AboutContain";
import TechnologyContain from "./contain/TechnologyContain";
import ProjectContain from "./contain/ProjectContain";
import CareerContain from "./contain/CareerContain";
import SocialContain from "./contain/SocialContain";
import BoardContain from "./contain/BoardContain";

const Main = styled.div`
    width: ${props=>props.size.width}px;
    height: ${props=>props.size.height}px;
    position: fixed;
    top: ${props=>props.position.top}px;
    left: ${props=>props.position.left}px;
    border-style: solid;
    border-color: ${({theme}) => theme.bd1};
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    transition: none;
    overflow: hidden;
    
    &.fullMode{
        background-color: ${props=>props.theme.bg1};
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        transition: 0.3s;
        border-radius: 0;
        border-style: none;
    }
`;

const Page = styled.div`
    width: ${props=>props.size.width}px;
    height: ${props=>props.size.height}px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: none;
    background-color: ${props=>props.theme.bg1};
    
    &.fullMode{
        transform: scale(${props =>props.fullsize.height/props.size.height});
        transition: 0.3s;
        border-radius: 0;
        border-style: none;
    }
`;

const Link = styled.div`
    position: absolute;
    left: 99.9%;
    top: 5%;
    width: 13%;
    height: 90%;
    z-index: 3;
    transition: none;

    &.fullMode{
        transition: 0.3s;
        background-color: '#00000020';
        position: fixed;
        top: 0;
        left: 99.5%;
        width: 9%;
        height: 100%;
        border-left-style: solid;
        border-color: ${({theme}) => theme.bd1};
        border-radius: 0;
    }
    
    &.fullMode:hover{
        left: 91.1%;
    }
`;

const FullButton = styled.div`
    width: 5.5vw;
    height: 2.5vw;
    position: fixed;
    bottom: calc(6vh + 2.5vw);
    right: 2vw;
    border: none;
    cursor: pointer;
    background-color: ${props=>props.theme.dark?'#AAAAAA':'#FFCA42'};
    border-top-left-radius: 5000px;
    border-bottom-left-radius: 5000px;
    border-top-right-radius: 5000px;
    border-bottom-right-radius: 5000px;
    font-weight: 700;
    color: ${props=>props.theme.dark?'black':'white'} !important;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const FullfalseButton = styled.div`
    position: fixed;
    top: 1.5vw;
    left: 2vw;
    font-size: 1.5vw;
    cursor: pointer;
    z-index: 10;
    color: ${props=>props.theme.dark?'white':'black'} !important;
    ${props=>props.$fullMode?'':'display: none;'}
`;

const linkes = [
    'About',
    'Project',
    'Skill',
    'Career',
    'Social',
    'Board'
];

function Container(props) {
    const [page, setPage] = useState('About');
    const [fullMode, setFullMode] = useState();
    const [modeChanged, setModeChanged] = useState();
    const mainRef = useRef();
    const [fullsize, setFullsize] = useState({ width: 0, height: 0 });
    const [aboutindex, setAboutindex] = useState(0);

    const pageChange = (link) => {
        setPage(link);
    }

    const pages = 
        page === 'About' ? <AboutContain $fullMode={fullMode} aboutindex={aboutindex} setAboutindex={setAboutindex}/> :
        page === 'Project' ? <ProjectContain /> :
        page === 'Skill' ? <TechnologyContain position={props.position} size={props.size}/> :
        page === 'Career' ? <CareerContain /> :
        page === 'Social' ? <SocialContain/> :
        page === 'Board' && <BoardContain/>
    ;

    useEffect(()=>{
        const handleResize = () => {
            const mainElement = mainRef.current;
            if (mainElement) {
                setFullsize({
                    width: window.innerWidth,
                    height: window.innerHeight
                });
            }   
        };
        
        setTimeout(() => {
            handleResize();
        }, 1);
        window.addEventListener("resize", handleResize);
        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, [mainRef]);

    useEffect(() => {
        if(props.isLogined){
            setPage('Board');
        }
    },[props.isLogined]);

    return (
        <>
        <Main
            position={props.position}
            size={props.size}
            className={modeChanged&&(fullMode&&'fullMode')}
            ref={mainRef}
            fullsize={fullsize}
        >
            <Page
                className={modeChanged&&(fullMode&&'fullMode')}
                size={props.size}
                fullsize={fullsize}
            >
                {pages}
            </Page>
        </Main>
        <Link
            className = {fullMode&&'fullMode'}
        >
            {linkes.map((link, index) => (
                <LinkButton
                    key = {index}
                    onClick = { ()=>pageChange(link) }
                    className = {
                        fullMode ? 'fullMode' :
                        link === page && 'selected'
                    }
                    $about = {index===0}
                    aboutindex = {aboutindex}
                >
                    {link}
                </LinkButton>
            ))}
        </Link>
        <FullButton
            onClick={()=>{
                setFullMode(true);
                setModeChanged(true);
            }}
            >FullMode</FullButton>
        <FullfalseButton
            onClick={()=>setFullMode(false)}
            $fullMode={fullMode}
        >X</FullfalseButton>
        </>
    );
}

export default Container;