import React, {useContext, useState} from "react";
import styled from "styled-components";
import Project from "./Project";
import { IsDarkContext } from "../../context/IsDarkContext";

const Main = styled.div`
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;
const Top = styled.div`
    width: 100%;
    height: 10%;
    border-width: 0.25vw;
    border-bottom-style: double;
    border-color: gray;
    border-radius: 0;
    position: absolute;
    top: 0;
    transition: height 1s, top 0.5s;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    background-color: ${({theme}) => theme.bg1};

    &.hide{
        height: 0;
        top: -10%;
    }
`;

const Title = styled.h1`
    font-size: 1.5em;
    color: ${({theme}) => theme.dark ? '#ac9ee9' : '#6646f3'} !important;
    transition: none;
    cursor: pointer;
    position: relative;

    &:hover{
        color: #f3b23a !important;
    }

    & .click{
        position: absolute;
        font-size: 1em;
        left: 110%;
        white-space: nowrap;
        font-weight: lighter;
        ${props=>props.$first?'':'display:none;'}
    }
`;

const Button = styled.img`
    margin: 0 1vw;
    height: 80%;
    ${props=> props.$abled && 'cursor: pointer;'}
    ${props=> props.$abled || 'opacity:0.5;'}
`;


function ProjectContain(props){
    const [num, setNum] = useState(1);
    const [isScrollingDown, setIsScrollingDown] = useState(false);
    const [proChanged, setProChanged] = useState(false);
    const isDark = useContext(IsDarkContext);

    const title = [
        "오늘 뭐먹지?",
        "Star",
        "Cyworld_for_JSP",
        "Mini MapleStory"
    ];

    const proLink = [
        "https://github.com/KSJ0314/What_to_eat_today",
        "https://github.com/KSJ0314/Star",
        "https://github.com/KSJ0314/Cyworld_for_JSP",
        "https://github.com/KSJ0314/Mini-MapleStory"
    ];

    const selectLink = (link)=>{
        if(link){
            window.open("about:blank").location.href = link;
        }
    }

    return (
        <Main>
            <Top className={isScrollingDown ? 'hide' : ''}>
                <Button
                    src = {isDark?'./images/lArrowW.png':'./images/lArrow.png'}
                    onClick={()=>{
                        if(num>1){
                            setNum(num-1);
                            setProChanged(true);
                        }
                    }}
                    $abled = {num>1}
                />
                <Title
                    onClick={()=>selectLink(proLink[num-1])}
                    $first = {num===1}
                >
                    <div className="click">-&gt; Click!</div>
                    {title[num-1]}
                </Title>
                <Button
                    src = {isDark?'./images/rArrowW.png':'./images/rArrow.png'}
                    onClick={()=>{
                        if(num<proLink.length){
                            setNum(num+1);
                            setProChanged(true);
                        }
                    }}
                    $abled = {num<proLink.length}
                />
            </Top>
            <Project
                scrollDown={setIsScrollingDown}
                num={num-1}
                proChanged={proChanged}
                setProChanged={setProChanged}
            />
        </Main>
    );
}

export default ProjectContain;