import React, {useContext} from "react";
import styled from "styled-components";
import { IsDarkContext } from "../context/IsDarkContext";

const Outside = styled.div`
    width: 24.5%;
    height: 100%;
    background-color: ${({theme}) => theme.bg3};
    border-style: solid;
    border-color: ${({theme}) => theme.bd1};
    padding: 0.5vw;
    flex-shrink: 0 !important;
    display: flex;
    flex-direction: column;
`;

const Main = styled.div`
    background-color: ${({theme}) => theme.bg4};
    border-style: solid;
    border-color: ${({theme}) => theme.bd1};
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    padding: 0.5vw;

    & > div{
        width: 100%;
    }
`;

const ProfileImage = styled.div`
    height: 45%;
    border-style: dotted;
    border-color: ${({theme}) => theme.bd1};
    display: flex;
    align-items: center;
    overflow: hidden;
    justify-content: center;
    
    & > img{
        margin-top: 20%;
        width: 100%;
    }
`;

const Intro = styled.div`
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    & > div{
        height: 20%;
        display: flex;
    }

    & > div > img{
        max-height: 70%;
        margin: 0 0.5vw;
        border-radius: 0;
    }
`;

const LinkUrl = styled.select`
    background-color: ${({theme}) => theme.bg1};
    height: 7%;
    width: 98%;
    border-radius: 0;
`;

const SpaceBorder = styled.div`
    border-style: dashed;
    border-color: ${({theme}) => theme.bd1};
    border-bottom: none;
    margin: 1vw;
`;

function Profile(props) {
    const isDark = useContext(IsDarkContext);

    const selectLink = (e)=>{
        const link = e.target.value;
        if(link){
            window.open(link);
            e.target.selectedIndex = 0;
        }
    }

    return(
        <Outside>
            <Main>
                <ProfileImage>
                    <img src="./images/profile.jpg" alt="profile" />
                </ProfileImage>
                <SpaceBorder />
                <Intro>
                    <div>
                        <img src={isDark?"./images/birth2.png":"./images/birth.png"} alt="birth" />
                        <div>1997. 10. 19.</div>
                    </div>
                    <div>
                        <img src={isDark?"./images/home2.png":"./images/home.png"} alt="home" />
                        <div>나주시 빛가람동</div>
                    </div>
                    <div>
                        <img src={isDark?"./images/tell2.png":"./images/tell.png"} alt="tell" />
                        <div>010 - 2431 - 4307</div>
                    </div>
                    <div>
                        <img src={isDark?"./images/email2.png":"./images/email.png"} alt="email" />
                        <div>sojung017@naver.com</div>
                    </div>
                </Intro>
                <SpaceBorder />
                <LinkUrl
                    onChange={selectLink}
                >
                    <option value="">바로가기</option>
                    <option value="https://github.com/KSJ0314">GitHub</option>
                    <option value="https://velog.io/@ksj0314/series">Velog</option>
                    <option value="https://ksj0314.github.io/">이력서</option>
                </ LinkUrl>
            </Main>
        </Outside>
    );
}

export default Profile;