import React from "react";
import styled from "styled-components";
import careers from "../../data/career.json";
import awards from "../../data/award.json";
import specs from "../../data/spec.json"

const Main = styled.div`
    width: 90%;
    height: 100%;
    padding: 3% 0;
    overflow-y: scroll;
    border-radius: 0;
    display: flex;

    &::-webkit-scrollbar {
        width: 0;
    }

    &::-webkit-scrollbar {
        display: none;
    }

    & > div {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    & > div > div:not(.title){
        width: 95%;
    }
`;

const Title = styled.div`
    width: 100% !important;
    font-size: 1.8em;
    margin-bottom: 1vw;
    color: #8383ec !important;
    font-weight: bold;
`;

const Item = styled.div`
    display: flex;
    
    & *{
        font-size: 1.15em;
    }

    & .date{
        font-weight: bold;
        color: gray;
    }

    & > div:first-child{
        margin-right: 1vw;
        flex-shrink: 0;
        width: 30%;
    }

    & > div:nth-child(2){
        width: 100%;
    }

    & .sub{
        font-size: 0.8em;
        color: gray;
    }
`;

const DashLine = styled.div`
    background-image: linear-gradient(to right, #b3b3b3 60%, transparent 40%);
    background-size: 0.6vw 1vw;
    height: 0.05vw;
    border-radius: 0;
    margin: 1vw auto;
`;

const Line = styled.div`
    margin: 2.5vw auto 1.5vw;
    border-bottom-style: solid;
    border-bottom-color: #b3b3b3;
    width: 100% !important;
`;

const Ellipsis = styled.div`
    width: 1% !important;
    padding-bottom: 2%;
`;

function CareerContain(props){
    return (
        <Main>
            <div>
                <Title>
                    Spec
                </Title>
                {specs.map((spec, index)=>(
                    <div key={spec.id}>
                        <Item>
                            <div>
                                <div className="date">
                                    {spec.date}
                                </div>
                            </div>
                            <div>
                                <div>
                                    {spec.title}
                                </div>
                                <div className="sub">
                                    {spec.sub}
                                </div>
                            </div>
                        </Item>
                        {index<specs.length-1 && <DashLine />}
                    </div>
                ))}
                <Line />
                <Title>
                    Education
                </Title>
                {careers.map((career)=>(
                    <div key={career.id}>
                        <Item>
                            <div>
                                <div className="date">
                                    {career.date}
                                </div>
                                <div className="sub">
                                    {career.company}
                                </div>
                            </div>
                            <div>
                                <div>
                                    {career.title}
                                </div>
                            </div>
                        </Item>
                    </div>
                ))}
                <Line />
                <Title>
                    Award
                </Title>
                {awards.map((award)=>(
                    <div key={award.id}>
                        <Item>
                            <div>
                                <div className="date">
                                    {award.date}
                                </div>
                            </div>
                            <div>
                                <div>
                                    {award.title}
                                </div>
                                <div className="sub">
                                    {award.sub}
                                </div>
                            </div>
                        </Item>
                    </div>
                ))}
                <Line />
                <Ellipsis>
                    <ul>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </Ellipsis>
            </div>
        </Main>
    );
}

export default CareerContain;