import React, {useState, useEffect, useRef} from "react";
import styled from "styled-components";
import Profile from "./Profile";
import Title from "./Title";
import ContainerOutside from "./ContainerOutside";
import Help from "./Help";
import { DarkChangeButton } from "./Button";
import YTPlayer from "./YTPlayer";
import { useLocation } from "react-router-dom";

const Background_col = styled.div`
    background: repeating-linear-gradient(
        to right,
        ${({theme}) => theme.bg_l1},
        0.1vw,
        ${({theme}) => theme.bg_l2} 0,
        ${({theme}) => theme.bg_l2} 1.4vw
    );
    width: 100vw;
    height: 100vh;
    border-radius: 0;
`;

const Background_row = styled.div`
    background: repeating-linear-gradient(
        to bottom,
        ${({theme}) => theme.bg_l1},
        0.1vw,
        transparent 0,
        transparent 1.3vw
    );
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
`;

const SolidBox = styled.div`
    background-color: ${({theme}) => theme.bg1};
    border-style: solid;
    border-color: ${({theme}) => theme.bd1};
    width: 80%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1vw;
`;

const DottedBox = styled.div`
    border-style: dotted;
    border-color: ${({theme}) => theme.bd1};
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1vw;
`;

const MainBox = styled.div`
    background-color: ${({theme}) => theme.bg2};
    border-style: solid;
    border-color: ${({theme}) => theme.bd1};
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 1.5vw;

    & > div{
        height: 100%;
    }

    & * {
        color: ${({theme}) => theme.color};
    }
`;

const Main_R = styled.div`
    width: 100%;
    flex-shrink: 1;
    display: flex;
    justify-content: end;
`;

const Main_R2 = styled.div`
    width: 97%;
    height: 100%;
    display: flex;
    flex-direction: column;
`;

const Space_3 = styled.div`
    height: 3%;
    flex-shrink: 0;
    border: none !important;
`;

function Wrap(props) {
    const [isLogined, setIsLogined] = useState(false);
    const location = useLocation();
    
    useEffect(() => {
      const code = new URLSearchParams(location.search).get('code');
      setIsLogined(code!==null);
    }, [location.search]);

    const mainRef = useRef();
    const [widthBig, setWidthBig] = useState(false);
    const [size, setSize] = useState({ width: 0, height: 0 });

    useEffect(() => {
        const sizeCheck = () => {
            const mainElement = mainRef.current;
            if (mainElement) {
                setSize({
                    width: mainElement.offsetWidth,
                    height: mainElement.offsetHeight
                });
            }
        };
        setTimeout(() => {
            sizeCheck();
        }, 1);
    }, [size]);

    useEffect(() => {
        if (size.width > size.height){
            setWidthBig(true);
        } else{
            setWidthBig(false);
        }
    }, [size]);

    return (
        <>
        <YTPlayer />
        <Background_col ref={mainRef}>
            <Background_row>
                <SolidBox>
                    <DottedBox>
                        <MainBox>
                            <Profile/>
                            <Main_R>
                                <Main_R2>
                                    <Title/>
                                    <Space_3 />
                                    <ContainerOutside isLogined={isLogined}/>
                                </Main_R2>
                            </Main_R>
                        </MainBox>
                    </DottedBox>
                </SolidBox>
                <DarkChangeButton onClick={props.DarkToggle}/>
            </Background_row>
        </Background_col>
        <Help isLogined={isLogined} widthBig={widthBig}/>
        </>
    );
}

export default Wrap;